import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
    data() {
        return {
            // 
            alertText: 2,
            // tab
            navList:[
                {name:'',lan_name:''},
                {name:'',lan_name:''},
                {name:'',lan_name:''},
                {name:'',lan_name:''}
            ],
            // tab
            nowIndex:0,
            // swiper
            swiperOption:{
                initialSlide:0,
                autoplay:false,
                keyboardControl:true,
                autoHeight:true,
                observer:true,
                observeParents:true,
            }
        }
    },
    computed: {
        swiper() {
            return this.$refs.mySwiper.swiper
        }
    },
    components:{
        swiper,
        swiperSlide
    },
    mounted() {
        
    },
    methods: {
        // tab
        tabClick(index){
            this.nowIndex = index
            this.swiper.slideTo(index,1000,false)
        },
        // 
        touchEndFun(event){
            console.log(this.swiper.activeIndex)
            this.nowIndex = this.swiper.activeIndex
        },
        // 
        rightClickLink(bool){
            if(bool == 'info'){
                this.$router.push('/views/healthData/perinfo')
            }
            if(bool == 'history'){

            }
        }
    }
}



